import axios from 'axios'
const base = '/players'

const getService = async () => {
    const res = await axios.get(base)
    return res.data
}

const addService = async obj => {
    const res = await axios.post(base, obj)
    return res.data
}

const editService = async (id, obj) => {
    const res = await axios.put(`${base}/${id}`, obj)
    return res.data
}

const deleteService = async id => {
    axios.delete(`${base}/${id}`)
}

const toggleService = async (id, obj) => {
    const res = await axios.patch(`${base}/${id}`)
    return res.data
}

export default {getService, addService, editService, deleteService, toggleService}